pre {
    font-size: 100%;
    padding: 5px 10px;
    margin: 0 auto;
    color: #020202;
    background-color: #eeeeff;
    overflow: auto;
    border: 2px;
    border-radius:$border-radius;
    font-family: inherit;
}
code {
    background-color: #eeeeff;
    color: #020202;
    weight: normal;
    font-family: inherit;
}
figure.highlight {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: inherit;
}
.highlight {
    padding: 5px 10px;
    background-color: #eeeeff;
    color: #f8f8f2;
    font-family: inherit;
}
