span.anchor {
    display: none;
}
.sanchor {
    background: url(data:image/png;base64,);
    background-position: bottom right;
    background-repeat: no-repeat;
    padding: 14px 14px 0px 0px;
    color: $white;
    background-color: transparent;
    font-size: 1px;
}
.canchor {
    background:url(data:image/png;base64,);
    background-position: bottom right;
    background-repeat: no-repeat;
    padding: 12px 12px 0px 0px;
    color: $white;
    background-color: transparent;
    font-size: 1px;
}
.mail {
    display: none;
}
.sanchor:before {
    display: block;
    content: " ";
    margin-top: -110px;
    height: 110px;
    visibility: hidden;
}
p.source {
    text-align: right;
    font-size: 80%;
    cite {
        font-style: normal;
    }
}
.day {
    h2 { font-size: $h4; }
}
.comment {
    display: none;
}

.profile-picture {
    height: $image-size;
    width: $image-size;
    position: relative;
    float: right;
    border-radius: 50%;
    margin-top: - $image-size * 0.1;
}

.tdiary-profile {
    width: 70%;
}
.tdiary-profile-picture {
    border-radius: 50%;
}
.footer{
    display: block;
    margin: 0 auto;
    text-align: right;
    font-size: 80%;
    width: 100%;
    max-width: $width-4;
}
.gsearch {
    max-width: 40vw;
}

@media #{$breakpoint-xs} {
    .footer {
        padding: 0 $space-1;
    }
}
@media #{$breakpoint-sm-md} {
    .footer {
        padding: 0 $space-2;
    }

}
@media #{$breakpoint-md-lg} {
    .footer {
        padding: 0 $space-3;
    }
}
@media #{$breakpoint-lg} {
    .footer {
        padding: 0 $space-4;
    }
}
