html {
    height: 100%;
    -ms-text-size-adjust: 100%;
    font-size: $base-font-size;
    font-family: $font-webfonts;
}
body {
    margin: 0;
    line-height: $line-height-3;
    min-width: 315px;
    height: 100%;
    overflow-x: hidden;
    font-smooth: always;
    -webkit-font-smoothing: antialiased;
    padding-top: 5rem;
}
wrapper {
    min-height: 100%;
}
main {
    display: block;
    margin: 0 auto;
    max-width: $width-4;
}
header {
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: $width-4;
    box-shadow: 0 6px 10px -10px rgba(0,0,0,0.7);
}
footer {
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: $width-4;
}

.logo {
    border-radius: 80%;
}
.gsearch {
    width: 10rem;
    max-width: 40vw;
    font-family: $font-webfonts;
}
.title-font{
    font-family: "Comic Neue", "AoyagiKouzanTSubset", $font-webfonts;
}
hr {
    padding: 0;
    margin: 8px 0;
    border: 0;
    height: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.8);
}
.icon-envelope, .icon-key, .icon-keybase,
.icon-twitter, .icon-facebook, .icon-github,
.icon-mastodon, .icon-amp {
    svg {
        height: 1.2rem;
        width: 1.2rem;
    }
}
.icon-envelope{
    color:#242424;
}
.icon-key {
    color:#ff8c00;
}
.icon-keybase {
    color:#ff8c00;
}
.icon-twitter {
    color:#55acee;
}
.icon-facebook {
    color:#3B5998;
}
.icon-github {
    color:#4c4c4c;
}
.icon-mastodon {
    color:#3B5998;
}
.icon-amp {
    color:$orange;
}
.research {
    li:nth-child(even){
        background-color: #f5f5f5;
    }
}

.small{
    font-size: 80%;
}

.doi {
    font-size: 80%;
}

#table-of-contents {
    margin: 0;
    padding: 0;
    display: -webkit-box;
    display: flex;
    -webkit-box-align:center;
    align-items:center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    h2 {
        margin: 0;
        padding: 0;
        font-size: $h4;
        border: none;
        margin-right: $space-2;
    }
    h2:after {
        content: ":";
        padding: 0;
        margin: 0;
    }
    #text-table-of-contents {
        ul {
            margin: 0;
            padding: 0;
            li {
                list-style: none;
                padding: 0;
                display: inline-block;
                + li:before {
                    $nbsp: "\00a0";
                    content: "/#{$nbsp}";
                    padding: 0;
                    margin: 0;
                    color: #ccc;
                }
            }
        }
    }
}

a {
    text-decoration: none;
    color: $brand-success;
}
a:hover {
    text-decoration: underline;
}

@media #{$breakpoint-xs} {
    body {
        padding-top: 4.8rem;
    }
    .wrapper {
        padding: 0 $space-1;
        background-color: $white;
    }
    header {
        padding: 0 $space-1;
        background-color: $white;
        input.navigation-input {
            display: none;
        }
        nav{
            padding-right:$space-2;
        }
        div.breadcrumbs {
            padding-right:$space-2;
        }
        .navcontents {
            height: 0;
            padding: 0;
            margin: 0;
            opacity: 0;
            overflow: hidden;
        }
        input.navigation-input:checked ~ .navcontents {
            position: absolute;
            -webkit-box-orient:vertical;
            -webkit-box-direction:normal;
            flex-direction:column;
            top: 2rem;
            width: 80vw;
            left: 10vw;
            text-align: right;
            padding: 10px;
            padding-right:$space-2;
            height: auto;
            opacity: 1;
            background-color: #fff;
            box-shadow: 0 6px 10px 0 rgba(0,0,0,0.7);
            div {
                font-size: $h3;
                margin: 10px auto;
                width: 100%;
                text-align: right;
            }
        }
    }
    .dmenu{
        input.dmenu-input {
            display:none;
            width: 0;
        }
        label {
            text-decoration: none;
            color: $brand-success;
        }
        input.dmenu-input:not(:checked) ~ .dcontents {
            display: none;
        }
        input.dmenu-input:checked ~ .dcontents {
            opacity: 1;
            background-color: #fff;
            ul {
                background-color: #fff;
            }
        }
    }
    .page-title {
        font-size: $h2;
    }
    .icon-envelope, .icon-key, .icon-keybase,
    .icon-twitter, .icon-facebook, .icon-github,
    .icon-mastodon, .icon-amp {
        svg {
            height: 1.4rem;
            width: 1.4rem;
        };
    }
    amp-img.profile-picture {
        height: $image-size * 0.5;
        width: $image-size * 0.5;
        position: relative;
        float: right;
        border-radius: 50%;
        margin-right: 10px;
        margin-top: -$image-size * 0.2;
        margin-left: $image-size * 0.2;
    }
    #table-of-contents {
        display: none;
    }
    footer {
        padding: 0 $space-1;
    }
}
