h1 {
    font-weight: normal;
}
.content {
    em {
        font-weight: normal;
        font-style: normal;
    }
    strong {
        font-weight: normal;
        font-size: $h2;
    }
    .alert {
        font-weight: bold;
        color: $red;
    }
    a:hover {
        text-decoration: underline;
    }
    ol {
        counter-reset: list;
    }
    ol > li {
        position: relative;
        list-style: none;
        margin-top: 5px;
        margin-bottom: 5px;
    }
    dd {
        padding: 0;
        margin-right: 0;
        margin-left: $base-font-size;
    }
    ul > li {
        margin: 5px 0 5px 0;
    }
    ul {
        padding: 0 20px 0 20px;;
    }
    ol {
        padding: 0 30px 0 30px;
    }
    ol > li:before {
        content: "(" counter(list, decimal) ") ";
        counter-increment: list;
        position: absolute;
        left: -35px;
    }
    blockquote {
        // border: 1px solid black;
        border-left: 5px solid #333333;
        margin: 10px 0 10px 0;
        padding: 0px 8px 0px 8px;
        font-style: normal;
    }
    h2 {
        border-bottom: 1px solid #5af;
        font-weight: normal;
        margin: $space-2 0 0 0;
        padding: 0;
    }
    h3 {
        border-bottom: 1px dashed #5af;
        font-weight: normal;
        margin: $space-2 0 0 0;
    }
    h4 {
        border-bottom: 1px dotted #5af;
        font-weight: normal;
        margin: $space-2 0 0 0;
    }
    h5 {
        border-bottom: 1px dashed #333333;
        font-weight: normal;
        font-size: $h4;
        margin: $space-2 0 0 0;
    }
    h6 {
        border-bottom: 1px dotted #333333;
        font-weight: normal;
        font-size: $h4;
        margin: $space-2 0 0 0;
    }
}
